import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { GoogleLogin } from 'react-google-login';

import { startGoogleLogin, StartLogin } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';

import '../../styles/style.scss';


export const LoginScreen = () => {

    const dispatch = useDispatch();

    const [formLoginValues, handleLoginInputChange] = useForm({
        lEmail: 'test20@test.com',
        lPassword: '123456'

    });
    const { lEmail, lPassword } = formLoginValues;


    const handleLogin = (e) => {
        e.preventDefault();

        dispatch(StartLogin(lEmail, lPassword));

    }

    const handleGoogleLogin = (response) => {

        const { tokenId } = response;

        dispatch(startGoogleLogin(tokenId));
    }



    return (
        <div className="login-container">
            <div>
                <div>
                    <h3>Ingreso</h3>
                    <form
                        onSubmit={handleLogin}
                    >
                        <div>
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Email"
                                name='lEmail'
                                value={lEmail}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div>
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contrasea"
                                name='lPassword'
                                value={lPassword}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div>
                            <input
                                type="submit"
                                className="btnSubmit"
                                value="Login"
                            />
                        </div>
                    </form>

        
                    <GoogleLogin
                        clientId="263099325228-55asn431srakct5pegne7a7go6hjctq6.apps.googleusercontent.com"
                        render={renderProps => (
                            <button onClick={renderProps.onClick} disabled={renderProps.disabled}>This is my custom Google button</button>
                        )}
                        buttonText="Login"
                        onSuccess={handleGoogleLogin}
                        onFailure={handleGoogleLogin}
                        cookiePolicy={'single_host_origin'}
                    />

                    

                    <Link className='Link' to="/register"> No tienes una cuenta ? - Registrarse </Link>
                </div>
            </div>
        </div>
    )
}

