import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { clearActiveBinProduct, clearActiveBinUser, startLoadBinProducts } from '../../actions/Bin';

import '../../styles/style.scss';
import { BinProductsDashboardTable } from './tables/Bin/BinProductsDashboardTable';


export const BinProductsDashboard = () => {

    const dispatch = useDispatch();

    useEffect(() => {
        
        dispatch( clearActiveBinProduct() );
        dispatch( clearActiveBinUser() );
    
      
    }, []);

    return (
        <div className='dashboard__container'>


            <div>
                <BinProductsDashboardTable />

            </div>

        </div>
    );
};