import { fetchConToken, fetchSinToken } from "../helpers/fetch";
import { types } from "../types/types";
import { uiCloseProgressBackdrop, uiOpenErrorAlert, uiOpenProgressBackdrop, uiOpenSuccessAlert } from "./ui";



export const startLoadCategories = () => {
    return async (dispatch) => {


        try {
            const resp = await fetchSinToken('categories');
            const body = await resp.json();


            if (body.categories) {

                const categories = body.categories;

                console.log(categories)

                dispatch(loadCategories(categories));

            } else {
                return console.log(body.msg);
            }


        } catch (error) {
            return console.log(error);
        }
    }
}

const loadCategories = (categories) => ({
    type: types.loadCategories,
    payload: categories
})

export const startCreateCategory = (name) => {

    return async (dispatch) => {

        if (name === '' || !name) {

            return;
        } else {

            try {

                const resp = await fetchConToken('categories', name, 'POST');
                const body = await resp.json();

                if (body.msg === "OK") {

                    dispatch(startLoadCategories());

                    dispatch(uiCloseProgressBackdrop());

                    dispatch(uiOpenSuccessAlert('Category has been successfully created!'));

                } else {

                    dispatch(uiCloseProgressBackdrop());
                    dispatch(uiOpenErrorAlert('Error trying to create the category! Talk to developer'));
                    console.log(body.msg);
                }

            } catch (error) {
                dispatch(uiCloseProgressBackdrop());
                dispatch(uiOpenErrorAlert('Error trying to create the category! Talk to developer'));
                console.log(error);
            }

        }

    }




}

export const categoryStartUpdated = (category) => {
    return async(dispatch) => {

        try {


            const resp = await fetchConToken( `categories/${category._id}`, category , 'PUT' );
            const body = await resp.json();

            console.log(body);


            if( body.msg === 'OK' ){

                dispatch( startLoadCategories() );
                
                dispatch( uiCloseProgressBackdrop() );

                dispatch( uiOpenSuccessAlert('Category has been successfully updated!') );


            } else {
                dispatch( uiCloseProgressBackdrop() );
                dispatch(uiOpenErrorAlert('Error trying to update the category! Talk to developer'));
                console.log(body.msg);
            }


        } catch (error) {
            dispatch( uiCloseProgressBackdrop() );
            dispatch(uiOpenErrorAlert('Error trying to update the category! Talk to developer'));
            console.log(error);
        }

    }
}

export const categoryStartDeleted = (category) => {
    return async(dispatch) => {

        try {

            dispatch( uiOpenProgressBackdrop() );


            const resp = await fetchConToken( `categories/${category._id}`,{}, 'DELETE' );
            const body = await resp.json();

            console.log(body);


            if( body.msg === "OK" ){
                
                dispatch( startLoadCategories() );
                
                dispatch( uiCloseProgressBackdrop() );

                dispatch( uiOpenSuccessAlert('Category has been successfully removed!') );


            } else {
                dispatch( uiCloseProgressBackdrop() );
                dispatch(uiOpenErrorAlert(`${body.msg}`));
                console.log(body.msg);
            }


        } catch (error) {
            dispatch( uiCloseProgressBackdrop() );
            dispatch(uiOpenErrorAlert('Error trying to delete the category! Talk to developer'));
            console.log(error);
        }

    }
}