import React from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';

import { CardsMainHome } from '../dashboard/mainCards/CardsMainHome';

export const Home = () => {
    return (
        <>
            <Container>
                <Box sx={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    margin: 'auto',
                    marginTop: '2.5%',
                    marginLeft: '2.5%',
                    
                }}>

                        <CardsMainHome />

                </Box>
            </Container>
        </>
    );
};