import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Swal from 'sweetalert2';

import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import IconButton from '@mui/material/IconButton';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import EditIcon from '@mui/icons-material/Edit';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import TextField from '@mui/material/TextField';
import Container from '@mui/material/Container';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import { Button } from '@mui/material';
import ImageCropDialog from "./ImageCropDialog";
import sortArray from 'sort-array';
import AddIcon from '@mui/icons-material/Add';

import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';

import { Image } from 'cloudinary-react';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';


import { clearActiveProduct, productStartUpdated } from '../../../actions/products';
import { uiCloseProductModalEdit, uiCloseProgressBackdrop, uiOpenDialogFields, uiOpenErrorAlert, uiOpenProgressBackdrop } from '../../../actions/ui';

import { verifyProductFields } from '../../../helpers/verifyProductFields';
import { uploadImageToCloudinary } from '../../../helpers/uploadCloudinary';
import { startCreateCategory } from '../../../actions/categories';
import { styles__productModal } from '../../../styles/styles__productModal';



export const EditProductModal = () => {

    const theme = useTheme();
    const sm = useMediaQuery(theme.breakpoints.down('sm'));
    const md = useMediaQuery(theme.breakpoints.down('md'));
    const xl = useMediaQuery(theme.breakpoints.down('xl'));
    const lg = useMediaQuery(theme.breakpoints.down('lg'));

    const imageRef = useRef();

    const dispatch = useDispatch();

    const { activeProduct } = useSelector(state => state.product);
    const { modalProductEdit } = useSelector(state => state.ui);

    const { categories } = useSelector(state => state.categories);


    const initEvent = {
        name: '',
        price: '',
        description: '',
        img: '',
        quantity: '',
        category: {
            name: ''
        }
    }

    const [loadImage, setLoadImage] = useState(null);
    const [categoryValue, setCategoryValue] = useState('');
    const [formValues, setFormValues] = useState(initEvent);

    const [cars, setCars] = useState([]);
    const [counterImage, setCounterImage] = useState(1);
    const [selectedCar, setSelectedCar] = useState(null);



    useEffect(() => {

        if (activeProduct) {

            setFormValues(activeProduct);
            setCategoryValue(activeProduct.category.name)
            setCars(activeProduct.img)

        } else {
            setFormValues(initEvent);
        }

    }, [activeProduct, setFormValues]);

    useEffect(() => {

        setFormValues({
            ...formValues,
            category: {
                ...category,
                name: categoryValue
            }
        })


    }, [categoryValue]);

    const { name, price, quantity, category, description, img } = formValues;

    const onCancel = () => {
        setSelectedCar(null);
    };

    const setCroppedImageFor = (id, crop, zoom, aspect, croppedImageUrl) => {
        const newCarsList = [...cars];
        const carIndex = cars.findIndex((x) => x.id === id);
        const car = cars[carIndex];
        const newCar = { ...car, croppedImageUrl, crop, zoom, aspect };
        newCarsList[carIndex] = newCar;
        setCars(newCarsList);
        setSelectedCar(null);
    };

    const resetImage = (id) => {
        setCroppedImageFor(id);
    };


    const handleProductInputChange = ({ target }) => {
        setFormValues({
            ...formValues,
            [target.name]: target.value
        })
    }

    const handleChangeImageFile = (car) => {

        const newCars = cars.filter(e => (e.id !== car.id) && e)


        setCars([
            ...newCars,
            {
                ...car,
                flag: 'true'
            }
        ])

        handleImageFile();
    }


    const handleClose = () => {

        setFormValues(initEvent);
        dispatch(clearActiveProduct());
        setCars([]);
        dispatch(uiCloseProductModalEdit());
        setCategoryValue('');

    };


    const handleImageFile = (e) => {
        imageRef.current.click();
    }


    const handleSelectInput = (e) => {

        setCategoryValue(e.target.value)

    }

    const handleNewCategory = async () => {

        setCategoryValue('');
        dispatch(uiCloseProductModalEdit());

        const { value: newCategory } = await Swal.fire({
            title: 'Create new category',
            input: 'text',
            inputLabel: 'Name of category',
            inputPlaceholder: 'Enter your new category'

        })

        dispatch(startCreateCategory(newCategory));
    }

    const handelChangeImage = (e) => {

        e.preventDefault();



        if (e.target.files[0]) {


            let reader = new FileReader();

            reader.readAsDataURL(e.target.files[0]);

            reader.onload = () => {

                const base64 = reader.result;

                var arrayAuxiliar = [];

                arrayAuxiliar = base64.split(',');


                // setLoadImage(arrayAuxiliar)

                if (cars.filter(e => e.flag === 'true')[0]?.flag === 'true') {


                    const oldCar = cars.filter(e => (e.flag === 'true') && e)

                    const newCar = {
                        id: oldCar[0].id,
                        imageUrl: arrayAuxiliar,
                        croppedImageUrl: null,
                    }


                    setCars(sortArray([
                        ...cars.filter(e => e.flag !== 'true'),
                        newCar
                    ], {
                        by: 'id',
                    }));

                    return;
                }

                if (cars.length >= 5) {
                    return console.log('Solo se pueden hasta 5 imagenes');
                }
                setCars([
                    ...cars,
                    {
                        id: (cars.length >= 1) ? cars.length + 1 : 1,
                        imageUrl: arrayAuxiliar,
                        croppedImageUrl: null,
                    }
                ])

            }

        }
    }


    const handleSubmit = async (e) => {
        e.preventDefault();


        try {



            const errors = verifyProductFields(formValues, cars);


            if (errors.length >= 1) {

                dispatch(uiOpenDialogFields(errors));

            } else {

            handleClose();

            dispatch(uiOpenProgressBackdrop());


            const [one = '', two = '', tree = '', four = '', five = ''] = cars;


            var [oneImage, twoImage, treeImage, fourImage, fiveImage] = await Promise.all([

                (one !== '') ? uploadImageToCloudinary(one.imageUrl) : '',
                (two !== '') ? uploadImageToCloudinary(two.imageUrl) : '',
                (tree !== '') ? uploadImageToCloudinary(tree.imageUrl) : '',
                (four !== '') ? uploadImageToCloudinary(four.imageUrl) : '',
                (five !== '') ? uploadImageToCloudinary(five.imageUrl) : '',
            ]);

            const oneNew = {
                id: 1,
                imageUrl: oneImage,
                croppedImageUrl: null
            }
            const twoNew = {
                id: 2,
                imageUrl: twoImage,
                croppedImageUrl: null
            }
            const treeNew = {
                id: 3,
                imageUrl: treeImage,
                croppedImageUrl: null
            }
            const fourNew = {
                id: 4,
                imageUrl: fourImage,
                croppedImageUrl: null
            }
            const fiveNew = {
                id: 5,
                imageUrl: fiveImage,
                croppedImageUrl: null
            }

            const newImages = sortArray([
                (oneNew.imageUrl !== '') && oneNew,
                (twoNew.imageUrl !== '') && twoNew,
                (treeNew.imageUrl !== '') && treeNew,
                (fourNew.imageUrl !== '') && fourNew,
                (fiveNew.imageUrl !== '') && fiveNew, 
            ].filter( e => (e !== false)), {
                by: 'id',
            })


            const data = formValues;
            data.img = newImages;

            dispatch(productStartUpdated(data));

        }
        } catch (error) {
            dispatch(uiCloseProgressBackdrop());
            dispatch(uiOpenErrorAlert('Error trying to create the product! Talk to developer'));
            console.log(error);
        }

    }


    return (


        <div>


            <Modal
                open={modalProductEdit}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Container maxWidth="sm">
                    <Box
                        component="form"
                        sx={styles__productModal(sm, md, lg, xl)}
                        noValidate
                        autoComplete="off"
                    >

                        <h1>Edit product</h1>
                        <Container id='container'>
                            <div>
                                <TextField
                                    required
                                    id="outlined-required"
                                    label="Name"
                                    name='name'
                                    value={name}
                                    onChange={handleProductInputChange}
                                />

                                <TextField
                                    required
                                    id="outlined-required"
                                    type="number"
                                    label="Price"
                                    name='price'
                                    value={price}
                                    onChange={handleProductInputChange}
                                />

                                <TextField
                                    required
                                    id="outlined-required"
                                    type="number"
                                    label="Quantity"
                                    name='quantity'
                                    value={quantity}
                                    onChange={handleProductInputChange}
                                />

                                <input
                                    type="file"
                                    className="products_input-image"
                                    placeholder="img"
                                    ref={imageRef}
                                    name='img'
                                    value={''}
                                    onChange={handelChangeImage}
                                />

                                {/* <Button
                                    variant="outlined"
                                    value="Upload Image"
                                    onClick={handleImageFile}
                                >Change Image</Button> */}


                                <FormControl id="FormControl">

                                    <InputLabel id="demo-simple-select-label">Category</InputLabel>

                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        label="Category"
                                        value={categoryValue}
                                        onChange={handleSelectInput}
                                    >
                                        {
                                            categories.map(e => (
                                                <MenuItem key={e._id} name={e.name} value={e.name}>{e.name}</MenuItem>
                                            ))
                                        }
                                        <MenuItem value='' name='' onClick={handleNewCategory} >New category<AddIcon /></MenuItem>
                                    </Select>

                                </FormControl>


                                <Container id='containerImage'>

                                    <IconButton
                                        disabled={(cars.length === 0 || cars.length === 1 || counterImage <= 1)}
                                        color="primary"
                                        component="span"
                                        onClick={() => setCounterImage(counterImage - 1)}
                                    >
                                        <ArrowLeftIcon id='ArrowIcon' />
                                    </IconButton>


                                    {selectedCar ? (
                                        <ImageCropDialog
                                            id={selectedCar.id}
                                            imageUrl={selectedCar.imageUrl}
                                            cropInit={selectedCar.crop}
                                            zoomInit={selectedCar.zoom}
                                            aspectInit={selectedCar.aspect}
                                            onCancel={onCancel}
                                            setCroppedImageFor={setCroppedImageFor}
                                            resetImage={resetImage}
                                        />
                                    ) : null}

                                    {
                                        (cars === [] || cars.length !== 0)
                                            ?
                                            <>

                                                <div id="imageCard" >
                                                    {cars.map((car) => (
                                                        <Container
                                                            key={car.id}
                                                            sx={{
                                                                visibility: (car.id === counterImage) ? 'visible' : 'hidden',
                                                            }}
                                                        >
                                                            {
                                                                (car.imageUrl !== String) 
                                                                    ?
                                                                    <img 
                                                                        alt='Product' 
                                                                        src={car.croppedImageUrl ? car.croppedImageUrl : car.imageUrl}
                                                                        onClick={() => {
                                                                            console.log(car);
                                                                            setSelectedCar(car);
                                                                        }}
                                                                        />
                                                                    :
                                                                    <Image
                                                                        cloudName="the-kings-company"
                                                                        publicId={car.croppedImageUrl ? car.croppedImageUrl : car.imageUrl}
                                                                        alt='Product'
                                                                        onClick={() => {
                                                                            console.log(car);
                                                                            setSelectedCar(car);
                                                                        }}
                                                                    />
                                                            }
                                                            <IconButton
                                                                sx={{
                                                                    position: 'absolute',
                                                                    top: '-2%',
                                                                    right: '30%'
                                                                }}
                                                                color="primary"
                                                                component="span"
                                                                onClick={() => handleChangeImageFile(car)}
                                                            >
                                                                <EditIcon />
                                                            </IconButton>
                                                            <IconButton
                                                                sx={{
                                                                    position: 'absolute',
                                                                    top: '-2%',
                                                                    right: '25.5%'
                                                                }}
                                                                color="inherit"
                                                                component="span"
                                                                onClick={handleImageFile}
                                                            >
                                                                <PhotoCameraIcon />
                                                            </IconButton>
                                                        </Container>
                                                    ))}

                                                </div>

                                            </>
                                            :
                                            <div id="imageCard" >
                                                <div onClick={handleImageFile} id='NoneImage'><PhotoCameraIcon id='PhotoCameraIcon' /></div>
                                            </div>
                                    }



                                    <IconButton
                                        disabled={(cars.length === 0 || cars.length === 1 || counterImage >= 5 || counterImage >= cars.length)}
                                        color="primary"
                                        component="span"
                                        onClick={() => setCounterImage(counterImage + 1)}
                                    >
                                        <ArrowRightIcon id='ArrowIcon' />
                                    </IconButton>

                                </Container>




                                {/* <div id="imagePreviewProduct">

                                    {


                                        (formValues.img !== '' && loadImage === null && formValues.img)
                                        &&
                                        <Image cloudName="the-kings-company" publicId={formValues.img} alt='Product' />

                                    }
                                    {
                                        (loadImage !== null)
                                        &&
                                        <img alt='Product' src={`${loadImage}`} />
                                    }
                                    {
                                        (loadImage === null)
                                        &&
                                        <div onClick={handleImageFile} id='NoneImage'><PhotoCameraIcon id='PhotoCameraIcon' /></div>
                                    }
                                </div> */}


                                <div>
                                    <TextField
                                        className="description"
                                        required
                                        id="outlined-required"
                                        label="Description"
                                        name='description'
                                        value={description}
                                        onChange={handleProductInputChange}
                                    />

                                </div>
                            </div>
                        </Container>

                        <Container>
                            <Button
                                type="submit"
                                id="submitButton"
                                variant="contained"
                                onClick={handleSubmit}
                            >Save</Button>

                            <Button
                                id="submitButton"
                                variant="outlined"
                                onClick={handleClose}
                            >Close</Button>
                        </Container>

                    </Box>
                </Container>
            </Modal>
        </div>
    );
}
