import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import { styled, alpha } from '@mui/material/styles';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import AccountCircle from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LogoutIcon from '@mui/icons-material/Logout';
import MenuIcon from '@mui/icons-material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import useMediaQuery from '@mui/material/useMediaQuery';
import HomeIcon from '@mui/icons-material/Home';
import { useTheme } from '@mui/material/styles';
import SearchIcon from '@mui/icons-material/Search';
import { useDispatch, useSelector } from 'react-redux';
import { startChecking, startLogout } from '../../actions/auth';
import { useNavigate } from 'react-router';




const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      width: '42.5vw',
      '&:focus': {
        width: '45vw',
      },
    },
    [theme.breakpoints.between('sm', 'lg')]: {
      width: '28vw',
      '&:focus': {
        width: '28.5vw',
      },
    },
    [theme.breakpoints.down('sm')]: {
      width: '100',
    },
  }
}));


export const NavbarLogged = () => {

  const navigate = useNavigate();

  const { role, name } = useSelector(state => state.auth);

  const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      marginLeft: (role === 'USER_ROLE') ? theme.spacing('25%') : theme.spacing('20%'),
      width: 'auto',
    },
    [theme.breakpoints.down('lg')]: {
      marginLeft: (role === 'USER_ROLE') ? theme.spacing('20%') : theme.spacing(0),
      width: 'auto',
    },
    [theme.breakpoints.between('sm', 'md')]: {
      marginLeft: theme.spacing('17.5%'),
      width: 'auto',
    },
    [theme.breakpoints.down('sm')]: {
      width: '100%',
    },
  }));

  const dispatch = useDispatch();

  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleLogout = () => {
    handleClose();
    dispatch(startLogout());
  }

  const handleClose = () => {
    setAnchorEl(null);
  };

  const [anchorMenu, setAnchorMenu] = React.useState(null);

  const handleResponsiveMenu = (event) => {
    setAnchorMenu(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorMenu(null);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        sx={{
          position: 'static',
          backgroundColor: '#154585'
        }}>
        <Toolbar>

          {
            (sm)
            &&
            <>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                onClick={handleResponsiveMenu}
                sx={{ mr: 2 }}
              >
                <MenuIcon />
              </IconButton>

              <Menu
                id="menu-appbar"
                anchorEl={anchorMenu}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                sx={{
                  width: '90vw',
                  marginTop: '7%',
                  marginLeft: '3%',
                  '& .MuiMenuItem-root': {
                    width: '100vw'
                  }
                }}
                open={Boolean(anchorMenu)}
                onClose={handleCloseMenu}
              >
                <MenuItem onClick={handleCloseMenu}><AccountCircle />  Profile</MenuItem>
                {
                  (role === 'USER_ROLE')
                  &&
                  <MenuItem onClick={handleCloseMenu}><ShoppingCartIcon />  My Cart</MenuItem>
                }
                <MenuItem onClick={handleLogout}><LogoutIcon />  Logout</MenuItem>
              </Menu>
            </>
          }

          {
            (!sm)
            &&
            <>

              <IconButton
                color="inherit"
                edge="start"
                onClick={() => navigate('/', { replace: true })}
                sx={{
                  marginRight: '36px',
                }}
              >
                <HomeIcon />

              </IconButton>
              {
                (role !== 'USER_ROLE')
                &&
                <Typography
                  variant="h6"
                  component="div"
                  onClick={() => navigate('/dashboard', { replace: true })}
                  sx={{
                    flexGrow: 1,
                    cursor: 'pointer',
                    fontSize: (md) && '15px'
                  }}>

                  Panel de control

                </Typography>
              }

            </>
          }


          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>


          {
            (!sm)
            &&
            <>
              <Typography
                variant="h6"
                component="div"
                sx={{ flexGrow: 1 }}>
              </Typography>





              {
                (role === 'USER_ROLE')
                &&
                <IconButton
                  size="large"
                  edge="end"
                  color="inherit"
                  sx={{ mr: 2 }}
                >
                  <ShoppingCartIcon />
                </IconButton>
              }





              <div>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  sx={{
                    marginTop: (md) ? '4%' : '2.5%',
                    marginLeft: (md) ? '-4%' : '-2.5%',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                >
                  <MenuItem onClick={handleClose}>Profile</MenuItem>
                  <MenuItem onClick={handleLogout}>Logout</MenuItem>
                </Menu>
              </div>
              <Typography
                edge="end"
                variant="p"
                component="div"
                sx={{ textAlign: 'end' }}>
                {name}
              </Typography>
            </>
          }


        </Toolbar>
      </AppBar>
    </Box>
  );
}

