import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import { styled, alpha } from '@mui/material/styles';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import AccountCircle from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LogoutIcon from '@mui/icons-material/Logout';
import MenuIcon from '@mui/icons-material/Menu';
import HomeIcon from '@mui/icons-material/Home';
import MenuItem from '@mui/material/MenuItem';
import AssignmentIndIcon from '@mui/icons-material/AssignmentInd';
import Menu from '@mui/material/Menu';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import SearchIcon from '@mui/icons-material/Search';
import { useNavigate } from 'react-router';
 

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing('25%'),
    width: 'auto',
  },
  [theme.breakpoints.between('sm', 'md')]: {
    marginLeft: theme.spacing('17.5%'),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '42.5vw',
      '&:focus': {
        width: '45vw',
      },
    },
  }
}));


export const NavbarUnlogged = () => {

  const navigate = useNavigate();

  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));


  const handleToLogin = () => {
    handleClose();

    navigate('/login', { replace: true })
  }

  const handleToRegister = () => {
    handleClose();

    navigate('/register', { replace: true })
  }


  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const [anchorMenu, setAnchorMenu] = React.useState(null);

  const handleResponsiveMenu = (event) => {
    setAnchorMenu(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorMenu(null);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        sx={{
          position: 'static',
          backgroundColor: '#154585'
        }}>
        <Toolbar>

          {
            (sm)
            &&
            <>
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                onClick={handleResponsiveMenu}
                sx={{ mr: 2 }}
              >
                <MenuIcon />
              </IconButton>

              <Menu
                id="menu-appbar"
                anchorEl={anchorMenu}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                sx={{
                  width: '90vw',
                  marginTop: '7%',
                  marginLeft: '3%',
                  '& .MuiMenuItem-root': {
                    width: '100vw'
                  }
                }}
                open={Boolean(anchorMenu)}
                onClose={handleCloseMenu}
              >
                <MenuItem onClick={handleToLogin}><AccountCircle />Login</MenuItem>
                <MenuItem onClick={handleToRegister}><AssignmentIndIcon />Register</MenuItem>
              </Menu>
            </>
          }

          {
            (!sm)
            &&
            <>

              <IconButton
                color="inherit"
                edge="start"
                onClick={() => navigate('/', { replace: true })}
                sx={{
                  marginRight: '36px',
                }}
              >
                <HomeIcon />

              </IconButton>
            </>
          }



          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>


          {
            (!sm)
            &&
            <>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              </Typography>

              <div>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  sx={{
                    marginTop: (md) ? '4%' : '2.5%',
                    marginLeft: (md) ? '-4%' : '-2.5%',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                >
                  <MenuItem onClick={handleToLogin}><AccountCircle />Login</MenuItem>
                  <MenuItem onClick={handleToRegister}><AssignmentIndIcon />Register</MenuItem>
                </Menu>
              </div>
            </>
          }

        </Toolbar>
      </AppBar>
    </Box>
  );
}

