

export const styles__tables = (sm, md, lg, xl) => {
    switch (true) {

        case sm:
            return tables__smBox();

        case md:
            return tables__mdBox();

        case lg:
            return tables__lgBox();

        case xl:
            return tables__xlBox();

        default:
            break;
    }
}


const tables__xlBox = () => ({
    position: 'absolute',
    left: '12.5%',
    top: '12.5%',
    height: 550,
    width: 0.8,
    '& .super-app-theme--header': {
        backgroundColor: '#154585',
        color: 'rgba(255, 255, 255)',


    },
    textAlign: 'center'
});

