import * as React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    useLocation,
    useNavigate
} from "react-router-dom";


import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';


import Fab from '@mui/material/Fab';

import Box from '@mui/material/Box';
import InventoryIcon from '@mui/icons-material/Inventory';
import GroupIcon from '@mui/icons-material/Group';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';

import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

import HomeIcon from '@mui/icons-material/Home';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AutoDeleteIcon from '@mui/icons-material/AutoDelete';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import ArrowCircleUpIcon from '@mui/icons-material/ArrowCircleUp';
import { uiOpenProductModal, uiOpenProductModalEdit, uiOpenUserModalAdd, uiOpenUserModalEdit, uiOpenDialogDelete } from '../../../actions/ui';
import { productBinStartEnable, userBinStartEnable } from '../../../actions/Bin';


export const NavigationMenuMovil = () => {

    const navigate = useNavigate();
    const { pathname } = useLocation();

    const dispatch = useDispatch();

    const { activeUser } = useSelector(state => state.users);
    const { activeProduct } = useSelector(state => state.product);

    const { activeBinUser } = useSelector(state => state.bin);
    const { activeBinProduct } = useSelector(state => state.bin);



    const handleAddButton = () => {

        switch (pathname) {
            case '/dashboard/products':
                dispatch(uiOpenProductModal())
                break;

            case '/dashboard/users':
                dispatch(uiOpenUserModalAdd())
                break;


            default:
                break;
        }

    }
    const handleEditButton = () => {

        switch (pathname) {
            case '/dashboard/products':
                dispatch(uiOpenProductModalEdit())
                break;

            case '/dashboard/users':
                dispatch(uiOpenUserModalEdit())
                break;


            default:
                break;
        }

    }
    const handleDeleteButton = () => {

        switch (pathname) {

            case '/dashboard/products':
                dispatch(uiOpenDialogDelete())
                break;

            case '/dashboard/users':
                dispatch(uiOpenDialogDelete())
                break;


            default:
                break;
        }

    }



    const handleBinEnable = () => {
        switch (pathname) {

            case '/dashboard/bin/products':
                dispatch(productBinStartEnable(activeBinProduct))
                break;

            case '/dashboard/bin/users':
                dispatch(userBinStartEnable(activeBinUser))
                break;


            default:
                break;
        }
    }

    const handleBinDelete = () => {
        switch (pathname) {

            case '/dashboard/bin/products':
                dispatch(uiOpenDialogDelete())
                break;

            case '/dashboard/bin/users':
                dispatch(uiOpenDialogDelete())
                break;


            default:
                break;
        }
    }


    const [anchorEl, setAnchorEl] = React.useState(null);
    const [anchorHome, setAnchorHome] = React.useState(null);
    const handleOpenMenuBin = Boolean(anchorEl);
    const handleOpenMenuHome = Boolean(anchorHome);

    const handleClick = (event) => {

        if (!!anchorEl) {
            setAnchorEl(null);
        } else {
            setAnchorEl(event.currentTarget);
        }
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    const handleClickHome = (event) => {

        if (!!anchorHome) {
            setAnchorHome(null);
        } else {
            setAnchorHome(event.currentTarget);
        }
    };
    const handleCloseHome = () => {
        setAnchorHome(null);
    };

    return (
        <>
            <BottomNavigation sx={{
                position: 'fixed',
                bottom: 0,
                width: '100vw',
                borderTop: '1px solid #B2B6B5',
                zIndex: (theme) => theme.zIndex.drawer + 1

            }} value={pathname}>

                <BottomNavigation value={pathname} onClick={handleClick} >

                    <BottomNavigationAction
                        label="Bin"
                        value={
                            (pathname === "/dashboard/bin/users")
                                ?
                                "/dashboard/bin/users"
                                :
                                "/dashboard/bin/products"
                        }
                        icon={<AutoDeleteIcon />}
                    />

                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={handleOpenMenuBin}
                        onClose={handleClose}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={() => navigate('/dashboard/bin/products', { replace: true })} >Products</MenuItem>
                        <MenuItem onClick={() => navigate('/dashboard/bin/users', { replace: true })} >Users</MenuItem>
                    </Menu>

                </BottomNavigation>

                <BottomNavigationAction
                    label="Products"
                    value="/dashboard/products"
                    icon={<InventoryIcon />}
                    onClick={() => navigate('/dashboard/products', { replace: true })}
                />

                <BottomNavigation value={pathname} onClick={handleClickHome} >
                    <BottomNavigationAction
                        label="Home"
                        value={
                            (pathname === "/dashboard")
                                ?
                                "/dashboard"
                                :
                                "/"
                        }
                        icon={<HomeIcon />}
                    />


                    <Menu
                        id="basic-menu"
                        anchorEl={anchorHome}
                        open={handleOpenMenuHome}
                        onClose={handleCloseHome}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={() => navigate('/dashboard', { replace: true })} >Dashboard</MenuItem>
                        <MenuItem onClick={() => navigate('/', { replace: true })} >User home</MenuItem>
                    </Menu>

                </BottomNavigation>

                <BottomNavigationAction
                    label="Users"
                    value="/dashboard/users"
                    icon={<GroupIcon />}
                    onClick={() => navigate('/dashboard/users', { replace: true })}
                />

                <BottomNavigationAction
                    label="Regist"
                    value="/dashboard/regist"
                    icon={<LibraryBooksIcon />}
                    onClick={() => navigate('/dashboard/regist', { replace: true })}
                />



                <Box sx={{
                    position: 'absolute',
                    right: 0,
                    bottom: '20vh',
                    width: '60px',
                    display: 'flex',
                    flexWrap: 'wrap',
                    '& .MuiFab-root': {
                        marginTop: '2vh',
                        marginRight: '1vh'
                    }
                }}>

                    {
                        (pathname === '/dashboard/products' || pathname === '/dashboard/users')
                        &&
                        <>
                            {
                                (!!activeUser || !!activeProduct)
                                &&
                                <>
                                    <Fab onClick={handleDeleteButton}>
                                        <DeleteIcon />
                                    </Fab>


                                    <Fab color="secondary" aria-label="edit" onClick={handleEditButton}>
                                        <EditIcon />
                                    </Fab>
                                </>
                            }
                            <Fab color="primary" aria-label="add" onClick={handleAddButton}>
                                <AddIcon />
                            </Fab>
                        </>
                    }

                    {
                        (pathname === '/dashboard/bin/products' || pathname === '/dashboard/bin/users')
                        &&
                        <>
                            {
                                (!!activeBinProduct || !!activeBinUser)
                                &&
                                <>
                                    <Fab color="secondary" aria-label="edit" onClick={handleBinDelete}>
                                        <DeleteForeverIcon />
                                    </Fab >

                                    <Fab onClick={handleBinEnable}>
                                        <ArrowCircleUpIcon />
                                    </Fab>

                                </>
                            }
                        </>
                    }

                </Box>


            </BottomNavigation>
        </>
    );
}
