import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { DataGrid } from '@mui/x-data-grid';
import { Box } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';

import { activeBinProduct } from '../../../../actions/Bin'

import { prepareProducts } from '../../../../helpers/prepareProducts';

import '../../../../styles/style.scss';

import { styles__tables } from '../../../../styles/styles__tables';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

const columns = [
  {
    field: 'name',
    headerName: 'Name',
    width: 120,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'price',
    headerName: 'Price',
    type: 'number',
    width: 80,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'quantity',
    headerName: 'Quantity',
    type: 'number',
    width: 90,
    headerClassName: 'super-app-theme--header'
  },
  {
    field: 'user',
    headerName: 'User',
    width: 80,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'category',
    headerName: 'Category',
    width: 130,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'description',
    headerName: 'Description',
    width: 500,
    headerClassName: 'super-app-theme--header'
  },

];


export const BinProductsDashboardTable = () => {

  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));

  const dispatch = useDispatch();

  const { binProducts } = useSelector(state => state.bin);


  if (binProducts !== undefined) {

    var binProductsPrepared = prepareProducts(binProducts);
  }



  const onRowClick = (event) => {

    const productSelected = (binProducts.filter(
      e => (e._id === event.row.id)
    ));


    dispatch(activeBinProduct(productSelected[0]))
  }

  return (
    <div>




      <div>
        <Box
          sx={styles__tables(sm, md, lg, xl)}>
          {
            (binProducts !== undefined)
              ? <DataGrid
                rows={binProductsPrepared[0]}
                columns={columns}
                pageSize={8}
                rowsPerPageOptions={[8]}
                onRowClick={onRowClick}

              />
              :

              <>
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation={false} />
              </>

          }

        </Box>
      </div>
    </div>
  );
}
