

export const styles__productModal = (sm, md, lg, xl) => {
    switch (true) {

        case sm:
            return productModal__smBox();

        case md:
            return productModal__mdBox();

        case lg:
            return productModal__lgBox();

        case xl:
            return productModal__xlBox();

        default:
            break;
    }
}


const productModal__xlBox = () => ({
    '& .MuiTextField-root': {
        m: 1,
        width: '25ch',
    },
    '& #container': {
        display: 'flex',
        flexWrap: 'wrap',
        marginTop: '2.5%',
    },
    '& .description': {
        position: 'relative',
        width: '95%',
        left: 0
    },
    bgcolor: '#fff',
    height: '95vh',
    marginTop: '2.5%',
    borderRadius: '5px',
    h1: {
        fontSize: '25px',
        paddingTop: '1%',
        borderBottom: '1px solid #B2B6B5',
        marginLeft: '4%',
        marginRight: '4%',
        marginBottom: '2.5%',
        paddingBottom: '1%'
    },
    div: {

        textAlign: 'center',
    },
    '& #FormControl': {
        m: 1,
        width: '25ch',
    },
    '& #imagePreviewProduct': {
        margin: 'auto',
        marginTop: '2.5%',
        width: '30ch',
        img: {


            width: '30ch',
            height: '30ch',
            objectFit: 'contain',

        }
    },
    '& #submitButton': {
        width: '20%',
        margin: 'auto',
        marginTop: '1vh',
        marginRight: '2vw',
    },
    '& #NoneImage': {
        position: 'relative',
        width: '30ch',
        height: '30ch',
        objectFit: 'contain',
        border: 'gray 1px dashed',
        ":hover": {
            cursor: 'pointer'
        }
    },
    '& #PhotoCameraIcon': {
        fontSize: '50px',
        color: 'gray',
        marginTop: '40%'
    },
    '& .MuiIconButton-root': {
        marginTop: '25%',
        height: '50px',
        width: '50px'
    },
    '& #containerImage': {
        display: 'flex',
        flexWrap: 'wrap',
    },
    '& #ArrowIcon': {

        fontSize: '50px',

    
    },
    '& #imageCard': {
        margin: 'auto',
        marginTop: '2.5%',
        width: '30ch',
        height: '30ch',
        img: {
            position: 'absolute',
            left: '35%',
            width: '30ch',
            height: '30ch',
            objectFit: 'contain',
        }
    },
});

