import React from 'react';
import { Route, Routes, useLocation } from 'react-router';

import { ProductsDashboard } from './ProductsDashboard';
import { RegistDashboard } from './RegistDashboard';
import { UsersDashboard } from './UsersDashboard';
import { BinProductsDashboard } from './BinProductsDashboard';
import { BinUsersDashboard } from './BinUsersDashboard';

import { ErrorAlert } from './alerts/ErrorAlert';
import { SuccessAlert } from './alerts/SuccessAlert';
import { DialogDelete } from './alerts/DialogDelete';
import { ProgressBackdrop } from './progress/ProgressBackdrop';
import { DialogFields } from './alerts/DialogFields';
import { CategoriesModal } from './modals/CategoriesModal';
import { CardProducts } from './mainCards/CardProducts';

import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import { Container } from '@mui/material';


export const Dashboard = () => {

    const { pathname } = useLocation();

    return (
        <>
            <CategoriesModal />
            <ProgressBackdrop />
            <DialogDelete />
            <DialogFields />
            <ErrorAlert />
            <SuccessAlert />

            <Routes>
                <Route path="products" element={<ProductsDashboard />} />
                <Route path="users" element={<UsersDashboard />} />
                <Route path="regist" element={<RegistDashboard />} />
                <Route path="bin/products" element={<BinProductsDashboard />} />
                <Route path="bin/users" element={<BinUsersDashboard />} />
            </Routes>


            {
                (pathname === '/dashboard')
                &&
                <CardProducts />
            }
        </>

    );
};

