import React from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { DataGrid } from '@mui/x-data-grid';
import { Box } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';

import '../../../styles/style.scss';
import { prepareRecords } from '../../../helpers/prepareRecords';
import { activeRecord } from '../../../actions/records';
import { uiOpenRecordModal } from '../../../actions/ui';
import { styles__tables } from '../../../styles/styles__tables';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';


const columns = [
  {
    field: 'name',
    headerName: 'Name',
    width: 120,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'action',
    headerName: 'Action',
    width: 120,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'user',
    headerName: 'User',
    width: 120,
    headerClassName: 'super-app-theme--header'
  },
  {
    field: 'type',
    headerName: 'Type',
    width: 120,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'date',
    headerName: 'Date',
    width: 250,
    headerClassName: 'super-app-theme--header'
  },

];


export const RecordsDashboardTable = () => {

  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));


  const dispatch = useDispatch();

  const { records } = useSelector(state => state.records);


  if (records !== null) {

    var recordsPrepared = prepareRecords(records);
  }




  const onRowClick = (event) => {

    const recordSelected = (records.filter(
      e => (e._id === event.row.id)
    ));


    dispatch(activeRecord(recordSelected[0])) 
  }

  const onRowDoubleClick = () => {
    dispatch( uiOpenRecordModal() )
  }

  return (
    <div>




      <div>
        <Box
          sx={styles__tables(sm, md, lg, xl)}>
          {
            (records !== null)
              ? <DataGrid
                rows={recordsPrepared[0]}
                columns={columns}
                pageSize={8}
                rowsPerPageOptions={[8]}
                onRowClick={onRowClick}
                onRowDoubleClick={onRowDoubleClick}
                hideFooterSelectedRowCount

              />
              :
              <>
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation={false} />
              </>
          }

        </Box>
      </div>
    </div>
  );
}
