
export const styles__cardsMain = ( sm, md, lg, xl) => {
  switch (true) {

    case sm:
      return cardsMain__smBox();

    case md:
      return cardsMain__mdBox();

    case lg:
      return cardsMain__lgBox();

    case xl:
      return cardsMain__xlBox();

    default:
      break;
  }
}


const cardsMain__xlBox = () => ({
  display: 'flex',
  flexWrap: 'wrap',
  width: '82.5vw',
  height: '80vh',

  marginTop: '10%',
  marginLeft: '2.5%',

  '& #container': {
    backgroundColor: '#fff',
    display: 'flex',
    flexWrap: 'wrap',
    width: '40vw',
    height: '30vh',
    marginBottom: '2.5%',
    border: '1px solid #B2B6B5',
    borderRadius: '3px',
  },
  '& .MuiListItemText-root': {
    display: 'inline',
    textAlign: 'center',
    height: '2.5vh',
  },
  '& #MiembrosDelEquipo': {
    width: '100%',
    marginTop: '-2.5vh',
    textAlign: 'start',
  },
  '& .MuiListItemText-secondary': {
  },
});

