import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { clearActiveBinProduct, clearActiveBinUser, startLoadBinUsers } from '../../actions/Bin';

import '../../styles/style.scss';
import { BinUsersDashboardTable } from './tables/Bin/BinUsersDashboardTable';


export const BinUsersDashboard = () => {

    const dispatch = useDispatch();


    useEffect(() => {

        dispatch(clearActiveBinProduct());
        dispatch(clearActiveBinUser());


    }, []);

    return (
        <div className='dashboard__container'>


            <div>
                <BinUsersDashboardTable />

            </div>



        </div>
    );
};