import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { Image } from 'cloudinary-react';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { useSelector } from 'react-redux';
import { startLoadCategories } from '../../../actions/categories';
import { prepareProducts } from '../../../helpers/prepareProducts';
import { Container, Divider } from '@mui/material';

export const CardsMainHome = () => {

    const { productRows } = useSelector(state => state.product);

    if (productRows !== undefined) {
        var cleanProducts = prepareProducts(productRows);

        console.log(cleanProducts[0]);

    }

    return (
        <>
            {
                (productRows !== undefined)
                &&
                cleanProducts[0].map(e => (
                    <Card
                        key={e.id}
                        sx={{
                            width: '30ch',
                            height: '40ch',
                            cursor: 'pointer',
                            marginBottom: '2.5%',
                            marginLeft: '2.5%',
                            objectFit: 'contain',
                            overflow: 'cover',
                            '& #productImage':{
                                width: '30ch',
                                height: '30ch',
                                marginLeft: 0,
                                marginRight: '5vh',
                            },
                            '& #title':{
                                marginTop: '2.5%',
                                visibility: 'hidden'
                            },
                            '& #price':{
                                marginTop: '7.5%',
                            },
                            ':hover':{
                                '& #title':{
                                    visibility: 'visible',
                                },
                                height: '42.5ch',
                            }

                        }}>



                            {
                                (e.img[0].imageUrl !== undefined)
                                    ?
                                    <Image
                                        id='productImage'
                                        cloudName="the-kings-company"
                                        publicId={e.img[0].imageUrl}
                                        alt='Product'
                                    />
                                    :
                                    <Image
                                        id='productImage'
                                        cloudName="the-kings-company"
                                        publicId={e.img[0]}
                                        alt='Product' />
                            }


                        <CardContent>
                            <Typography id='price' sx={{fontSize: '18px'}} gutterBottom variant="h5" component="div">
                            {`$${Number(e.price)}`}
                            </Typography>

                            <Typography id='title' variant="body2" color="text.secondary">
                            {e.description}
                            </Typography>
                        </CardContent>
                    </Card>
                ))
            }
        </>

    );
}
