import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { DataGrid } from '@mui/x-data-grid';
import { Box } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';


import '../../../styles/style.scss';
import {  uiOpenUserModalEdit } from '../../../actions/ui';
import { prepareUser } from '../../../helpers/prepareUser';
import { activeUser } from '../../../actions/users';
import { styles__tables } from '../../../styles/styles__tables';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';


const columns = [
  {
    field: 'name',
    headerName: 'Name',
    width: 120,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'email',
    headerName: 'Email',
    width: 200,
    headerClassName: 'super-app-theme--header'
  },

  {
    field: 'role',
    headerName: 'Role',
    width: 150,
    headerClassName: 'super-app-theme--header'
  },
  {
    field: 'google',
    headerName: 'Google',
    width: 500,
    headerClassName: 'super-app-theme--header'
  },

];


export const UsersDashboardTable = () => {

  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down('sm'));
  const md = useMediaQuery(theme.breakpoints.down('md'));
  const xl = useMediaQuery(theme.breakpoints.down('xl'));
  const lg = useMediaQuery(theme.breakpoints.down('lg'));

  const dispatch = useDispatch();

  const { users } = useSelector(state => state.users);


  if (users !== undefined) {

    var usersRowsPrepared = prepareUser(users);

  }


  const onRowClick = (event) => {

    const userSelected = (users.filter(
      e => (e._id === event.row.id)
    ));


    dispatch(activeUser(userSelected[0]))
  }

  const onRowDoubleClick = () => {
    dispatch( uiOpenUserModalEdit() );
  }

  return (
    <div>




<div>
        <Box
          sx={styles__tables(sm, md, lg, xl)}>
          {
            (users !== undefined)
              ? <DataGrid
                rows={usersRowsPrepared[0]}
                columns={columns}
                pageSize={8}
                rowsPerPageOptions={[8]}
                onRowClick={onRowClick}
                onRowDoubleClick={onRowDoubleClick}

              />
              :

              <>
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation="wave" />
                <Skeleton height={80} animation={false} />
              </>

          }

        </Box>
      </div>
    </div>
  );
}
